/* Education style begins */
.Education_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;  
    align-items: flex-start;
    color: #f4f4f4;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.Content_container{

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
}

.Content_one, .Content_two{

   display: flex;
   justify-content: center;
   flex-direction: column;
   margin: 20px 10px;
}


.Title_subject{

    margin: 5px 0 5px 0px;
    font-size: 1.7rem;
    font-weight: 300;
    line-height: 1.5;
    letter-spacing: 1.5px;

}


.Title_uni{
    font-weight: 300;
    font-size: 1.5rem;
    margin: 0 0 5px 0px;
}

.show-case{

    font-size: 1.5rem;
    line-height: 1.5;
    letter-spacing: 1.5px;
    font-weight: 300;
    
}
/* Education css ends  */

/* Interest css starts */
.Skills-container{
    
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    

}
.ul{
    
    text-decoration: none;
    list-style-type: square;   
}
.ul > li{

    display: block;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.5;
    padding: 10px;
    font-size: 1.25rem;
    text-transform: uppercase;
    font-weight: 300;
    text-align: left;
    color: #f4f4f4;
    text-decoration: none;
}


.col-1, .col-2{
    flex: 0.5;
    display: flex;
    flex-direction: column;
    column-gap: 10px;
    justify-content: center;
}

/* Interest Css ends */

/* skills css begins */

/* Skills css */




@media screen and (max-width:570px){
    .Skills-container{
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        
    }
     h1, p{
        display: block;
        /* border: 1px solid #f4f4f4; */
        text-align: left;
        padding-left: 20px;
    }
  
    .skills{
        width: 100px;
    }

    .col-1, .col-2{
        display: flex;
        align-items: center;
        justify-content: space-around;
             
    }
    .col1{
       flex: 0.4;  
    }
    .col2{
        flex: 0.4;
    }
    .Titles{
        font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; 
        font-weight: 500;
        margin: 30px 0px 5px 5px;
    }
    .uni{
        
        text-decoration: wavy;
        margin: 10px;
    }
    .Project{
        flex: 100%;
    }
}
@media screen and (max-width:500px) {



    .Skills-container{

        width: 100%;
        display: flex;
        justify-content: center;
        gap : 0px;
        
    }
   
  
    .Project{
        flex: 100%;
    }

}
