.employment{
   
    width: 100%;
    min-height: 90vh;
    height: auto;
    color: #f4f4f4;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: auto;
    padding: auto;
   

}

.history{

    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
    flex-direction: column;
    color: #f4f4f4;
    margin: 0 4px;
    

}

.work_1, .work_2, .work_3{

    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 0 0 10px 0;
   

}

.title{

    font-size: 2rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #f4f4f4;
    text-align: left;
    font-weight: 400;
    letter-spacing: 1.5px;


}

.position{

    font-size: 1.15rem;
    letter-spacing: 1.25px;
    line-height: 1.5;
    font-weight: 400;
    
}

.desc{
    font-size: 1.15rem;
}

.list_activities{

    margin: 5px 0;
}

 .list_tasks{

    display: inline-block;
    text-decoration: none;
    margin: 4px;
    letter-spacing: 1.5px;
    line-height: 1.5;
    text-align: left;
    cursor: pointer;
}
.list_tasks:hover{

    color: #2EE59D;
    
}

@media (max-width: 570px) {
    .title{
        padding-left: 20px;
    }
}



