.Contact_container{
    
    /* position: relative;
    top:10vh;
    z-index: 0; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    height: auto;
    position: relative;
    top: 5vh;
    z-index: 0;
   
   
}
.contact{
    display: flex;
    width: 500px;
    height: 500px;
    margin: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
}
.contact h1{

    text-align: center;
    color: aliceblue;
    font-weight: 500;
    letter-spacing: 1.5px;
    font-size: 2rem;
}

.Form{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.Form input[type = text],.Form input[type = email], .Form textarea, .Form >button{
    display: block;
    border: none;
    width: 95%;
    border-radius: 10px;
    margin: 15px auto 10px auto;
    padding: 16px;
    color:rgb(28, 27, 27) ;
    text-align: center;
    background-color: rgb(247, 240, 240);
    letter-spacing: 2px;
    outline: none;
    font-size: 1.2rem;
    box-sizing: border-box;
}

input[type = text]:focus,.Form input[type = email]:focus{
    font-size: 1.2rem;
    display: block;
    border: none;
    width: 95%;
    letter-spacing: 2px;
    text-align: center;
    /* background-color: #46878a; */
}

input:focus ~ .floating-label,
input:not(:focus):valid ~ .floating-label{
  top: 25px;
  bottom: 10px;
  left: 100px;
  font-size: 11px;
  opacity: 1;
}

.floating-label {
  position: absolute;
  pointer-events: none;
  left: 50px;
  top: 50px;
  transition: 0.2s ease all;
}
.topText{
    
  position: relative; /*  make label relate to div  */
  padding-top: 10px;  /*  make space for label      */

}
.Form > textarea{
    height: 200px;
}

.Form > button{
    width: 95%;
    padding: 12px;
    font-size: 1rem;
    color:rgb(21, 19, 19);
    font-family: "SF Mono","Fira Code","Fira Mono","Roboto Mono",monospace;
    
}
.Form > button:hover{
    background-color: #46878a;
    color: #f4f4f4;
}


@media (max-width:500px) {
    .contact{
        display:flex;
        justify-content: center;
        align-items: center;
        width: 90%;
    }
    .Form{
        width: 100%;
    }
    .Form > button{
        padding: 10px;
    
    }
   
}


